.company .page {
    color: #000;
}
.company-item {
    margin: 0 auto;
    text-align:  center;
    p {
        font-size: 21px;
        text-align: left;
        margin-bottom: 65px;
    }
    h2 {
        font-size: 34px;
        font-weight: bold;
        margin: 50px 0 40px 0;
    }
}
.company-image {
    display: flex;
    justify-content: space-between;
    margin: 0 25px;
    padding-bottom: 40px;
    border-bottom: 1px solid #d3e3f6;
}

//media

@media screen and (max-width:1099.98px) {
    .company {
        margin-top: 20px;
        .company-image {
            flex-wrap: wrap;
            justify-content: center
        }
        .company-item {
            margin:  0 15px;
            padding: 0;
            p {
                font-size: 17px;
                margin-bottom: 20px;
            }
            h2 {
                font-size: 28px;
                margin: 0px 0 20px 0;
            }
        }
        .row {
            margin: 0;
        }
    }
}