.slider {
    background: #f3f3f3;
    padding-bottom: 40px;
    .card {
        border-radius: 10px;
        margin: 0 10px;
        .card-title {
            background: #3c3c3c;
            font-size: 21px;      
            border-radius: 10px 10px 0 0; 
            height: 45px;
            margin: 0;
            align-items: center; 
        }
        img {
            width: 100%;
        }
    }
    h2 {
        font-size: 30px;
        font-weight: bold;
        color: #000;
        text-align: center;
        padding: 20px 0 15px 0;
    }
    .card-body {
        text-align: center;
        button {
            background: #3e536a;
            color: #fff;
            width: 200px;
            font-size: 21px;
            height: 54px;
            &:hover {
                background: #488ada;
            }
        }
    }
}
.slider-items {
    margin: 0 auto;
}


//media

@media screen and (max-width:1023.98px) {
    .slider {
        .slick-prev, .slick-next {
            display: none !important;
        }
        .row {
            margin: 0;
        }
    }
}
@media screen and (min-width:1024px) and (max-width:1199.98px) {
    .slider {
        width: 90%;
        .card-body button {
            width: 160px;
        }
    }
}

@media screen and (min-width:1200px) and (max-width:1279.98px) {
    .slider {
        width: 100%;
        .card-body button {
            width: 160px;
        }
    }
}