.page {
    max-width: 1625px;
    color: #fff;
    font-family: "SegoeUI";
    margin: 0 auto;
}
.footer .page {
    height: 346px;
    background-color: #203042;
}
.footer-blocks {
    display: flex;
    justify-content: center;
    width: 100%;
}
.blocks-item, .blocks-item-payment {
    display: flex;
    flex-direction: column;
    text-align: center;
}
.blocks-item a {
    color: #fff;
    text-decoration: none;
    width: 65px;
    margin: 45px auto 0 auto;
    font-size: 21px;
    &:hover {
        text-decoration: underline;
        text-decoration-color: red;
    }
}
.blocks-item-payment {
    margin-top: 40px;
    line-height: 2.1;
    p {
        font-size: 21px;
    }
    a {
        color: #fff;
        text-decoration: none;
        font-size: 18px;
        &:hover {
            color: red;
        }
    }
}
.payment {
    margin-top: 30px;
    img {
        margin-left: 15px;
    }
}
.social {
    display: flex;
    margin: 0 auto;
    padding-top: 25px;
    margin-top: -1.5px;
    div {
        background: #4185d9;
        width: 25px;
        border-radius: 50%;
        height: 25px;
        margin-left: 10px;
        a {
            font-size: 14px;
            position: absolute;
            margin-left: -8px;
            margin-top: -1px;
            .fa-facebook-f {
                margin-left: 4px;
            }
            .fa-tumblr {
                margin-left: 3px;
            }
        }
    }
}


// media


@media screen and (max-width:767.98px) {
    .footer .page {
        height: 100%;
        padding-bottom: 20px;
    }
    .container-fluid {
        padding: 0;
    }
    .footer-blocks {
        flex-wrap: wrap;
    }
    .blocks-item {
        width: 50%;
    }
    .footer .row {
        margin: -1px 0 0 0;
    }
}

@media screen and (min-width:576px) and (max-width:991.98px) {
    .footer-blocks {
        flex-wrap: nowrap !important;
    }
}
