.calendar {
    background: url(../img/GG_2.jpg) no-repeat center;
    padding-bottom: 30px;
    .calendar-item {
        display: flex;
        margin: 0 auto;
        margin-top: 50px;
    }
    .carousel-indicators {
        top: 110%;
        li {
            width: 15px;
            height: 15px;
            border-radius: 50%;
            background: #fff;
            margin: 0 7px;
        }
        .active {
            background: red;
        }
    }
    .carousel-item {
        h2 {
            font-size: 30px;
            font-weight: bold;
        }
        p {
            font-size: 18px;
        }
    }
    form {
        border-radius: 10px;
        box-shadow: 0 0px 500px 50px #000 inset;
        width: 380px;
        height: 460px;
        h2 {
            font-size: 30px;
            text-align: center;
            border-bottom: 2px solid red;
            margin: 0 15px 15px 15px;
        }
        .form-control {
            option {
                text-align: center;
                height: 36px;
            }
        }
        .big {
            text-transform: uppercase;
            margin: 10px 15px 10px 15px;
        }
        .return {
            margin: 0 15px;
        }
    }
    .time {
        display: flex;
        margin-top: 15px;
        .wrapper {
            margin-top: 4px;
            background: #fff;
            border-radius: 7px;
            width: 100%;
            margin: 0 15px;
            a:hover {
                color: #fff !important;
            }
            li:hover {
                background: #37cfe8;
            }
        }
        .dropdown dd, .dropdown dt, .dropdown ul {
            margin: 0px;
            padding: 0px;
        }
        .dropdown dd ul li a span:first-child, .dropdown dt a span span:first-child {
            background-repeat: no-repeat;
            width: 25px;
            height: 30px;
            display: inline-block;
            margin: 0px 5px 0 0;
            vertical-align: top;
        }
        .dropdown dt a span {
            cursor: pointer;
            display: block;
            padding: 5px;
            white-space: nowrap;
        }
        .dropdown dt a img {
            position: relative;
            z-index: 1;
        }
        .dropdown dt a span span {
            display: inline-block;
            padding: 0;
        }
        .dropdown dt a span span:first-child {
            padding: 0;
        }
        .dropdown dd {
            position: relative;
        }
        .dropdown a, .dropdown a:visited {
            color: #000;
            text-decoration: none;
            outline: none;
        }
        .dropdown a:hover {
            color: #5d4617;
        }
        .dropdown dt a:hover, .dropdown dt a:focus {
            color: #5d4617;
        }
        .dropdown dt a {
            position: relative;
            background: #4185d9;
            display: block;
            padding-right: 20px;
            width:100%;
            background: transparent;
            border: none;
        }
        
        .dropdown dt a:before {
            content: "▾";
            height: 3px;
            position: absolute;
            right: 10px;
            top: 10px;
            font-size: 15px;
            width: 6px;
            z-index: 2;
            color: #000;
        }
        .dropdown dd ul {
            background: #fff;
            color: #fff;
            display: none;
            left: 0px;
            padding: 0px;
            position: absolute;
            width: 100%;
            list-style: none;
            max-height: 170px;
            border: 1px solid #000;
            top: 2px;
            border-radius: 7px;
            z-index: 99;
        }
        
        li a {
            font-size:13px;
        }
        
        li a span:nth-child(2) {
            line-height: 1.8;
            font-size: 18px;
            padding-left: 10px;
        }
        .dropdown dd ul::-webkit-scrollbar-track {
         -webkit-box-shadow: inset 0 0 1px rgba(0,0,0,0.3);
         border-left:1px solid rgba(0,0,0,0.1);
        }
        .dropdown dd ul::-webkit-scrollbar-thumb {
         background: rgba(0,0,0,0.4);
        }
        .dropdown dd ul::-webkit-scrollbar-thumb:window-inactive {
            background: blue;
        }
        .dropdown span.value {
            display: none;
        }
        .dropdown dd ul li a {
            padding: 5px;
            display: block;
            font-size: 12px !important;
        }
        .dropdown dd ul li a:hover {
            background-color: rgba(0,0,0,0.05);
        }
        dl.dropdown {
            display: inline-block;
            width: 100%;
            margin: 0;
        }
        dl.dropdown > span:nth-child(2) {
            overflow: hidden;
            white-space: nowrap;
            display: inline-block;
        }
        dl.dropdown dt span:nth-child(2) {
            color: rgba(0, 0, 0, 0.6);
            font-size: 14px;
            font-weight: bold;
            line-height: 1;
            color: #000;
            position: absolute;
            font-size: 21px;
            top: 8px;
            width: 65%;
            text-align: center;
        }
    }
    .butt {
        text-align: center;
        .btn-danger {
            width: 200px;
            height: 54px;
        }
    }
    .custom-control {
        margin-top: 7px;
    }
