//media

@media screen and (max-width:767.98px) {
    .calendar {
        background: url(../img/GG_2.jpg);
        padding-bottom: 80px !important;
        .calendar-item {
            flex-direction: column;
        }
        form {
            width: 100%;
            height: 490px;
        }
        .carousel-item {
            p {
                font-size: 14px;
                text-align: center;
            }
            h2 {
                font-size: 26px;
                text-align: center;
            }
        }
        .row {
            margin: 0;
        }
        .calendar-item-left {
            padding: 0;
        }
    }
}