.reviews {
    .row {
        display: block;
        margin: 0 auto;
        max-width: 1200px;
    }
    .page {
        background: url(../img/GG_1-new.jpg) no-repeat;
        height: 634px;
    }
    #carouselExampleIndicators {
        height: 480px;
        h2 {
            text-align: center;
            padding-bottom: 35px;
            padding-top: 50px;
        }
        .carousel-indicators {
            margin-right: 0;
            margin-left: 0;
            li {
                width: 15px;
                border-radius: 50%;
                height: 15px;
                margin-right: 7px;
                margin-left: 7px;
                background-color: #fff;
            }
            a {
                position: absolute;
                right: 0;
                margin-top: -11px;
                color: #fff;
                text-decoration: underline;
                font-size: 21px;
            }
        }
        .carousel-indicators .active {
            background-color: #4185d9;
        }
    }
    .carousel-inner {
        max-width: 1280px;
    }
    .flex {
        display: flex;
        .carousel-item-left {
            p {
                text-align: center;
                clear: both;
                margin-left: 20px;
            }
        }
    }
    .carousel-item-right:after {
        content: "";
        width: 0;
        height: 0;
        border: solid transparent;
        position: absolute;
        pointer-events: none;
        right: 100%;
        border-right-color: #fff;
        border-width: 17px;
        top: 50%;
        margin-top: -17px;
    }
    .carousel-item-right {
        background-color: #fff;
        color: #000;
        margin-left: 20px;
        padding: 40px 120px 0 60px;
        font-style: italic;
    }
    .w-100 {
        width: 150px !important;
        height: 150px;
        border-radius: 50%;
        float: right;
        margin: 15px 0 30px 0;
    }
    .rating {
        display: flex;
        justify-content: flex-end;
        margin-right: -90px;
        margin-top: 25px;
        p {
            margin-right: 30px;
            font-size: 18px;
        }
    }
}



// media

@media screen and (max-width:991.98px) {
    .reviews {
        height: 685px;
        .w-100 {
            margin: 0 auto 15px auto;
        }
        .carousel-item-left {
            display: flex;
            flex-direction: column;
            p {
                margin-left: 0 !important;
            }
        }
        .carousel-item-right {
            padding: 0;
            margin-left: 0 !important;
            p {
                padding: 10px 15px 0 15px;
                font-size: 14px;
            }
        }
        .flex {
            flex-wrap: wrap;
        }
        .rating {
            margin: 0 15px !important;
            p {
                margin-right: 30px;
                font-size: 18px !important;
                margin-top: -10px;
            }
        }
        .page {
            height: 750px !important;
        }
        .carousel-indicators {
            top: 125%;
            a {
                right: auto !important;
                margin-top: 20px !important;
            }
        }
        h2 {
            padding-top: 25px !important;
            padding-bottom: 20px !important;
        }
    }
}

@media screen and (min-width:576px) and (max-width:991.98px) {
    .reviews .carousel-item-right {
        margin-top: 20px;
    }
}

@media screen and (min-width:768px) and (max-width:991.98px) {
    .reviews .carousel-indicators {
        top: 110% !important;
    }
    .reviews {
        height: 620px !important;
    }
}

@media screen and (min-width:992px) and (max-width:1099.98px) {
    .reviews {
        .carousel-item-left {
            max-width: 100% !important;
            p {
                margin-left: 0 !important;
            }
        }
    }
    .reviews #carouselExampleIndicators .carousel-indicators a {
        margin-right: 40px;
    }
    .reviews .carousel-indicators {
        top: 120% !important;
    }
}