.accordion .row {
    justify-content: center;
    a.col-xl-9 {
        text-decoration: underline;
        display: flex;
        justify-content: flex-end;
        margin: 35px 100px 100px 0;
        font-size: 21px;
    }
}
.text {
    color: #000;
    text-align: center;
    padding: 50px 0 70px 0;
    font-size: 34px;
}


.accordionTitle h3 {
    color: #000;
    text-align: left;
    padding: 14px 12px;
    border-bottom: 1px solid #5693dd;
    cursor: pointer;
    margin-top: 0;
    padding-right: 40px;
    transition: 0.25s all;
    position: relative;
}
.accordionTitle p {
    color: #000;
    font-style: italic;
    width: 90%;
}
.accordionTitle h3.active {
    margin-bottom: 21px;
    border-bottom: 1px solid #e4eef9;
}
.accordionTitle h3.active > .collapseIcon {
    top: 125%;
    border-bottom: 1px solid #5693dd;
}
.accordionTitle h3.alignLeft {
	padding-left: 35px;
}
.accordionTitle > div {
	display: none;
	text-align: left;
	margin-bottom: 9px;
}
.accordionTitle .collapseIcon {
    position: absolute;
    right: 0px;
    color: #5693dd;
    top: 75%;
    font-size: 25px;
    font-weight: 300;
    transform: translate(0, -50%);
    border-top: 1px solid #5693dd;
    border-right: 1px solid #5693dd;
    border-left: 1px solid #5693dd;
    width: 30px;
    text-align: center;
}
.accordionTitle .collapseIcon.alignLeft {
	right: initial;
	left: 20px;
}

//media

@media screen and (max-width:767.98px) {
    .accordion {
        margin-bottom: -45px;
        .accordionTitle p {
            margin-left: 15px;
            width: 85%
        }
        .accordionTitle {
           padding: 0;
        }
        .row {
            margin: 0;
        }
    }
}