.use .page {
    color: #000 !important;
}
.main {
    border-bottom: 1px solid #e3e3e3;
    padding-bottom: 50px;
    margin: 0 auto;
    position: relative;
    margin-top: -604px;
}
.main-item {
    display: flex;
    font-size: 21px;
    h2 {
        font-size: 21px;
        color: #709edc;
        line-height: 1.5;
        margin-left: 10px;
    }
}
.main-item-left {
    margin-bottom: 15px;
}
.main-item-right {
    display: flex;
    font-size: 21px;
    justify-content: flex-end;
    margin-bottom: 15px;
    h2 {
        font-size: 21px;
        line-height: 1.5;
        margin-left: 10px;
    }
    p {
        color: #709edc;
        position: absolute;
        left: -25%;
    }
}
.main-img .imac {
    width: 340px;
}
.main-img .arrow {
    vertical-align: top;
    width: 225px;
    margin-left: 30px;
}
.main-left {
    padding: 0;
}
.main-left-top {
    margin-top: -100px;
}
.main-right-top {
    margin-top: 15px !important;
}
.main-right {
    padding: 0;
    margin-top: 102px;
}
.one, .two {
    display: flex;
}
.background {
    position: relative;
    height: 340px;
    background-color: #e3e3e3;
    width: 100%;
    margin-top: 15.5%;
}

//media

@media screen and (max-width:991.98px) {
    .use {
        .main-img .arrow {
            width: 105px;
            margin-left: 0px;
        }
        span {
            display: none;
        }
        .main-img .imac {
            width: 180px;
        }
        .main-item {
            h2 {
                font-size: 17px;
                margin: 5px 0 0 5px;
            }
        }
        .main-right {
            margin-top: 15px;
        }
        .main-item-right{
            justify-content: flex-start;
            padding: 0;
            p {
                left: 0;
                margin-right: 5px;
            }
            h2 {
                font-size: 17px;
                margin: 5px 0 0 0px;
            }
        }
        .one, .two {
            flex-direction: column;
            padding: 0;
        }
        .main-left-top {
            margin-top: 15px;
        }
        .background {
            display: none;
        }
        .row {
            margin: 0;
        }
        .main {
            margin: 0 auto;
        }
        .main-item-right p {
            position: relative;
            left: 0%;
        }
    }
}

@media screen and (min-width:768px) and (max-width:991.98px) {
    .use .one, .use .two {
        flex-direction: row !important;
    }
    .use .main-left-top {
        margin-top: 0 !important;
    }
    .use .main-right {
        margin-top: 0px !important;
    }
}

@media screen and (min-width:992px) and (max-width:1279.98px) {
    .main-img .imac {
        width: 225px;
    }
    .main {
        margin-top: -464px;
    }
    .background {
        height: 273px;
        margin-top: 18.5%;
    }
    .main-img .arrow {
        width: 220px;
    }
    span {
        display: none;
    }
    .main-item-right p {
        position: relative;
        left: 0%;
    }
}