.rent {
    background: url(../img/back-map.png) no-repeat center;
    padding-bottom: 30px;
    .page {
        color: #000;
        padding: 0 15px;
    }
}
.card-deck {
    margin: 10px auto 0 auto;
    padding: 0;
    justify-content: space-between;
    .card {
        margin: 0;
        width: 32%;
        height: 450px;
        flex: none;
        border-radius: 10px;
    }
}
.alert {
    background: #262626;
    margin:  0 auto;
    text-align: center;
    color: #fff;
    font-size: 30px;
    font-weight: bold;
    text-transform: uppercase;
    border-radius: 7px;
    margin-top: 30px;
}
.card-title {
    display: flex;
    justify-content: center;
    h5 {
        font-size: 30px;
    }
    img {
        height: 25px;
        position: absolute;
        right: 8%;
        top: 5%;
    }
}
.card-text {
    background: #ecedef;
    border-radius: 7px;
    margin-bottom: 10px;
    &:hover {
        background: red;
    }
    &:hover > a {
        color: #fff;
        p {
            border-bottom: 1px solid #fff;
        }
    }
    p {
        margin: 2px 10px;
        border-bottom: 1px solid #000;
        font-size: 21px;
    }
    a {
        text-decoration: none;
        color: #000;
        width: 100%;
        span {
            display: flex;
            justify-content: space-between;
        }
    }
}

// media

@media screen and (max-width:991.98px) {
    .rent {
        .card {
            width: 100%;
        }
    }
}