.header {
    background: #203042;
    height: 65px;
    .navbar {
        margin: 0 auto;
        height: 65px;
        img {
            margin-top: -30px;
        }
        .nav-item {
            font-size: 21px;
            font-family: Tahoma;
            a {
                padding: 0;
                margin-left: 39px;
                color: #fff;
                &:hover {
                    text-decoration: underline;
                    text-decoration-color: red;
                }
            }
        }
    }
}
.header-top {
    background: #4185d9;
    height: 30px;
    .information {
        display: flex;
        margin: 0 auto;
        .form-control {
            background: transparent;
            border: none;
            width: 110px;
            color: #fff;
            padding: 4px 0 0 0;
            height: 28px !important;
            cursor: pointer;
            option {
                background: #4185d9;
            }
        }
        .info-left {
            display: flex;
            justify-content: space-around;
            a {
                color: #fff;
                font-size: 18px;
                &:hover {
                    color: red;
                }
            }
            .forMobile {
                display: flex;
                justify-content: center;
            }
        }
        .info-right {
            display: flex;
            justify-content: center;
            padding: 0;
            .form-control {
                background: transparent;
                border: none;
                width: 60px;
                margin-right: 15px;
                height: 28px;
                padding-top: 4px;
            }
            .btn {
                padding: 0 10px;
                height: 30px;
                background: transparent;
                color: #fff;
                &:hover {
                    opacity: 0.7;
                }
                img {
                    margin-right: 10px;
                }
            }
        }
    }
    .wrapper {
        margin-top: 4px;
        a:hover {
            color: #fff !important;
        }
        li:hover {
            background: #37cfe8;
        }
    }
    .dropdown dd, .dropdown dt, .dropdown ul {
        margin: 0px;
        padding: 0px;
    }
    .dropdown dd ul li a span:first-child, .dropdown dt a span span:first-child {
        background-repeat: no-repeat;
        width: 9px;
        height: 13px;
        display: inline-block;
        margin: 3px 5px 0 0;
        vertical-align: top;
    }
    .dropdown dt a span {
        cursor: pointer;
        display: block;
        padding: 5px;
        white-space: nowrap;
    }
    .dropdown dt a img {
        position: relative;
        z-index: 1;
    }
    .dropdown dt a span span {
        display: inline-block;
        padding: 0;
    }
    .dropdown dt a span span:first-child {
        padding: 0;
    }
    .dropdown dd {
        position: relative;
    }
    .dropdown a, .dropdown a:visited {
        color: #4a535f;
        text-decoration: none;
        outline: none;
    }
    .dropdown a:hover {
        color: #5d4617;
    }
    .dropdown dt a:hover, .dropdown dt a:focus {
        color: #5d4617;
    }
    .dropdown dt a {
        position: relative;
        background: #4185d9;
        display: block;
        padding-right: 20px;
        width:110px;
        background: transparent;
        border: none;
    }
    
    .dropdown dt a:before {
        content: "▾";
        height: 3px;
        position: absolute;
        right: 6px;
        top: 1px;
        font-size: 15px;
        width: 6px;
        z-index: 2;
        color: #fff;
    }
    .dropdown dd ul {
        background: #4185d9;
        color: #fff;
        display: none;
        left: 0px;
        padding: 0px;
        position: absolute;
        width: 110px;
        list-style: none;
        max-height: 170px;
        border: 1px solid #000;
        top: -7px;
        z-index: 2;
    }
    
    li a {
        font-size:13px;
    }
    
    li a span:nth-child(2) {
        line-height: 2em;
    }
    .dropdown dd ul::-webkit-scrollbar-track {
     -webkit-box-shadow: inset 0 0 1px rgba(0,0,0,0.3);
     border-left:1px solid rgba(0,0,0,0.1);
    }
    .dropdown dd ul::-webkit-scrollbar-thumb {
     background: rgba(0,0,0,0.4);
    }
    .dropdown dd ul::-webkit-scrollbar-thumb:window-inactive {
        background: blue;
    }
    .dropdown span.value {
        display: none;
    }
    .dropdown dd ul li a {
        padding: 5px;
        display: block;
        font-size: 12px !important;
    }
    .dropdown dd ul li a:hover {
        background-color: rgba(0,0,0,0.05);
    }
    dl.dropdown {
        display: inline-block;
        width: 110px;
        margin: -3px 0 0 1px;
    }
    dl.dropdown > span:nth-child(2) {
        overflow: hidden;
        white-space: nowrap;
        display: inline-block;
    }
    dl.dropdown dt span:nth-child(2) {
        color: rgba(0, 0, 0, 0.6);
        font-size: 14px;
        font-weight: bold;
        line-height: 1.6em;
        color: #fff;
        position: absolute;
        top: 4px;
    }
}


// media

@media screen and (max-width:991.98px) {
    .header {
        .row {
            margin: 0;
        }
        .navbar-toggler {
            border-color: #fff !important;
            margin-top: -7px !important;
        }
        .navbar-collapse {
            z-index: 9999;
            background: #a4abb3;
        }
        .nav-item a {
            margin-left: 0px !important;
            text-align: center;
        }
        .navbar img {
            height: 65px;
            margin-top: -8px;
        }
    }
    .header-top {
        height: 100%;
        .row {
            margin: 0;
        }
        .information {
            display: block;
            .info-left {
                flex-direction: column;
                a {
                    text-align: center;
                }
            }
        }
    }
}

@media screen and (min-width:576px) and (max-width:991.98px) {
    .header-top .information .info-left {
        flex-direction: row !important;
        justify-content: space-between;
    }
    .navbar-nav {
        justify-content: space-between;
        flex-direction: row !important;
    }
}

@media screen and (min-width:992px) and (max-width:1199.98px) {
    .header {
        .nav-item a {
            margin-left: 21px !important;
            text-align: center;
        }
        .navbar img {
            height: 65px;
            margin-top: 0px;
        }
    }
    .header-top .information .info-right {
        justify-content: flex-end;
        margin-left: -200px;
    }
    .navbar-expand-lg .navbar-collapse {
        justify-content: flex-end;
    }
}

@media screen and (min-width:1200px) and (max-width:1279.98px) {
    .header {
        .nav-item a {
            margin-left: 21px !important;
            text-align: center;
        }
        .navbar img {
            height: 65px;
            margin-top: 0px;
        }
    }
    .navbar-expand-lg .navbar-collapse {
        justify-content: flex-end;
    }
    .header-top .information .info-right {
        padding: 0;
        justify-content: flex-end;
    }
    .header-top .information .info-left {
        padding: 0;
    }
}
@media screen and (min-width:1280px) and (max-width:1365.98px) {   
    .header-top .information .info-left {
        margin-left: 20px;
    }
}
@media screen and (min-width:1366px) and (max-width:1920px) {   
    .header-top .information .info-left {
        margin-left: 10px;
    }
    .navbar-expand-lg .navbar-collapse {
        justify-content: flex-end;
    }
}
